import React, {useState} from 'react';
import AlInfoCard from '../../../components/al-info-card';
import DoneOutlineIcon from '@mui/icons-material/DoneOutline';
import AlBtn from '../../../components/al-btn';
import AlInput from '../../../components/al-input';

const EmployerUsers = () => {
  const [isEditMode, toggleEditMode] = useState(false);
  const [userFormData, updateUserformData] = useState({
    addressLineOne: '',
    addressLinetwo: '',
    city: '',
    country: '',
    pincode: '',
  });
  const userDetailsElements = [
    {
      label: 'Full Name',
      id: 'gstNumber',
      type: '',
    },
    {
      label: 'Job Title',
      id: 'panNumber',
      type: '',
    },
    {
      label: 'Email',
      id: 'panNumber',
      type: '',
    },
    {
      label: 'Contact No',
      id: 'panNumber',
      type: '',
    },
    {
      label: 'User Type',
      id: 'panNumber',
      type: '',
    },
    {
      label: 'Privileges',
      id: 'panNumber',
      type: '',
    },
  ];
  const UserDetails = ({
    designation,
    email,
    privilege,
    userType,
    contactNo,
  }) => {
    return (
      <>
        <div className='info-card-info-text '>{designation}</div>

        <div className='info-card-info-text '>{email}</div>
        <div className='info-card-info-text '>{contactNo}</div>

        <div className='info-card-info-text t-bold'>User Type: {userType}</div>
        <div className='info-card-info-text t-bold'>
          Privilege's: {privilege}
        </div>
      </>
    );
  };
  const onActionItemClick = (action) => {
    if (action === 'edit') {
      toggleEditMode(true);
    }
  };
  const updateUserFormVal = (id, val) => {
    updateUserformData({
      ...userFormData,
      [id]: val,
    });
  };
  return (
    <div className='d-flex employer-bank-details'>
      {isEditMode ? (
        <div>
          <div className='sub-header'>Edit Details</div>
          <div className='d-flex'>
            {userDetailsElements.map((elm) => (
              <AlInput
                label={elm.label}
                onChange={(val) => updateUserFormVal(elm.id, val)}
              />
            ))}
          </div>
          <div className='d-flex action-btn'>
            <AlBtn
              label='Cancel'
              variant='outlined'
              onClick={() => toggleEditMode(false)}
            />
            <AlBtn label='Save' variant='contained' />
          </div>
        </div>
      ) : (
        <>
          <AlInfoCard
            title='Maneesh Kumar'
            onActionItemClick={onActionItemClick}
            restOfElm={
              <UserDetails
                designation='Web Designer'
                email='manesh@antlegs.com'
                contactNo='9626171661'
                userType='Admin'
                privilege='Manage'
              />
            }
            lastActivity='tuesDay'
          />
          <div className='add-users'>
            <AlBtn label='Add' variant='contained' />
          </div>
        </>
      )}
    </div>
  );
};

export default EmployerUsers;
