import React from 'react';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import './style.scss';

const AlMenu = ({ actionElm, triggerAction, menuItems }) => {
  const onMenuItemClick = (val) => {
    triggerAction(val);
  };
  return (
    <Menu
      id='demo-positioned-menu'
      aria-labelledby='demo-positioned-button'
      anchorEl={actionElm}
      open={actionElm ? true : false}
      onClose={triggerAction}
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
    >
      {menuItems?.map((item) => (
        <MenuItem onClick={() => onMenuItemClick(item.value)}>
          {<item.displayElm/>}
        </MenuItem>
      ))}
    </Menu>
  );
};

export default AlMenu;
