import React, { useState } from 'react';
import AlInput from '../../../components/al-input';
import Chip from '@mui/material/Chip';

const RecruiterProfile = () => {
  const [profileFormData, updateProfileFormData] = useState({
    fullName: '',
    displayName: '',
    industry: '',
    location: '',
    website: '',
    currency: '',
    contactName: '',
    designation: '',
    email: '',
    contactNo: '',
    gstNumber: '',
    panNumber: '',
  });
  const recruiterDetailsElements = [
    {
      label: 'Full Name',
      id: 'fullName',
      type: '',
    },
    {
      label: 'Nationality',
      id: 'displayName',
      type: '',
    },
    {
      label: 'Gender',
      id: 'industry',
      type: '',
    },
    {
      label: 'Date of Birth',
      id: 'location',
      type: '',
    },
    {
      label: 'LinkedIn Profile',
      id: 'website',
      type: '',
    },
    {
      label: 'Contact Number',
      id: 'currency',
      type: '',
    },
    {
      label: 'Email',
      id: 'currency',
      type: '',
    },
  ];
  const [industries, updateIndustries] = useState(['Banking', 'Information Technology'])
  const updateProfileFormVal = (id, val) => {
    updateProfileFormData({
      ...profileFormData,
      [id]: val,
    });
  };
  return (
    <div className='recruiter-profile'>
      <div className='sub-header'>Recruiter Details</div>
      <div className='d-flex'>
        {recruiterDetailsElements.map((elm) => (
          <AlInput
            label={elm.label}
            onChange={(val) => updateProfileFormVal(elm.id, val)}
          />
        ))}
      </div>
      {/* <div className='split-border'></div> */}
      <div className='sub-header'>Experience</div>
      <div className='d-flex'>
        <AlInput
          label='Recruitment Experience'
          onChange={(val) => updateProfileFormVal('experience', val)}
        />
      </div>
      {/* <div className='split-border'></div> */}
      <div className='sub-header'>Education Details</div>
      <div className='d-flex badge-values h-center'>
        {industries?.map((val) => (
          <Chip label={val} size='small' />
        ))}
      </div>
      {/* <div className='split-border'></div> */}
      <div className='sub-header'>Education Details</div>
      <div className='d-flex'>
        <AlInput
          label='Highest Qualification / Certification'
          onChange={(val) => updateProfileFormVal('education', val)}
        />
      </div>
    </div>
  );
};

export default RecruiterProfile;
