import React, { useState } from 'react';
import Button from '@mui/material/Button';
import AlTabs from '../al-tabs';
import AlSelect from '../al-select';
import './style.scss';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';

const AlDetails = ({
  tabs,
  logo,
  detailsHeader,
  otherDetails,
  status,
  statusItems,
}) => {
  const [expandDetails, toggleExpand] = useState(true);
  return (
    <div className='al-details'>
      <div className='details-header d-flex space-between'>
        <div className='d-flex'>
          <div className='image-holder'>
            {logo ? (
              <img src={logo} />
            ) : (
              <div className='details-info-header'>{detailsHeader}</div>
            )}
          </div>
          <div className='details-info'>
            {logo && <div className='details-info-header'>{detailsHeader}</div>}
            {otherDetails?.map((detail) => (
              <div className='other-details-info'>{detail}</div>
            ))}
          </div>
        </div>
        <div className='details-control d-flex'>
          <AlSelect selectedValue={status} menuItem={statusItems} />
          <div
            className='details-toggle'
            onClick={() => toggleExpand(!expandDetails)}
          >
            {expandDetails ? (
              <div className='d-flex-center'>
                Read Less <KeyboardArrowUpIcon />
              </div>
            ) : (
              <div className='d-flex-center'>
                Read More <KeyboardArrowDownIcon />
              </div>
            )}
          </div>
        </div>
      </div>
      {expandDetails && (
        <div className='details-tab'>
          <AlTabs tabs={tabs} />
        </div>
      )}
    </div>
  );
};

export default AlDetails;
