import React, { useState } from 'react';

import UserTypeToggle from './user-type-toggle';
import Button from '@mui/material/Button';

import CreateEmployer from './create-employer';
import CreateRecruiter from './create-recruiter';

import './createAccount.scss';

const CreateAccount = ({ toggleToLogin }) => {
  const [userType, setUserType] = useState('');
  return (
    <div className='create-account flex-column'>
      {!userType ? (
        <>
          <div className='create-account-info'>
            Join as an <span className='primary-blue '>Employer </span> or
            <span className='primary-blue'>Recruiter</span>
          </div>
          <div className='user-type--toggle w-100 d-flex-center'>
            <UserTypeToggle
              userType='Employer'
              onSelectUserType={setUserType}
            />
            <UserTypeToggle
              userType='Recruiter'
              onSelectUserType={setUserType}
            />
          </div>
          <div className='sign-in-toggle'>
            Already a member?{' '}
            <span className='primary-blue' onClick={() => toggleToLogin(true)}>
              Sign-In
            </span>
          </div>
          <Button variant='contained' className='primary-btn'>
            Back
          </Button>
        </>
      ) : userType === 'Employer' ? (
        <CreateEmployer />
      ) : (
        <CreateRecruiter />
      )}
    </div>
  );
};

export default CreateAccount;
