import './App.scss';
import { withRouter, Route, Redirect } from 'react-router-dom';
import { UserEntry } from './module';
import Routes from './routes';
import AlHeader from './components/al-header/alHeader';
import AlBreadcrumbs from './components/al-breadcrumbs';
import AlDrawer from './components/al-drawer';
import AlSideBarItem from './components/al-sidebar-item';
import { useState } from 'react';

const pageNameMap = {
  dashboard: 'Dashboard',
  'manage-jobs': 'Manage Jobs',
  'manage-interviews': 'Manage Interviews',
  'recruiter-details': 'Manage Recruiters',
  'manage-recruiters': 'Manage Recruiters',
  'manage-employers': 'Manage Employers',
  'employer-details': 'Manage Employer',
  'manage-projects': 'Manage Projects',
  'job-form': 'Job',
  'job-details': 'Manage Job',
  'manage-candidates': 'Manage Candidate',
  'candidate-details': 'Candidate Profile',
  'candidate-form': 'Candidate',
};

function App({ location }) {
  const [isDrawerOpen, toggleDrawer] = useState(false);
  console.log('location', location);

  const paths = location.pathname.split('/');

  let currentPageName = pageNameMap[paths[1]];
  if (['job-form', 'candidate-form'].includes(paths[1])) {
    if (paths.length === 2) {
      currentPageName = 'Add ' + currentPageName;
    } else {
      currentPageName = 'Edit ' + currentPageName;
    }
  }
  // if (location.pathname !== '/login' && !localStorage.getItem('userdetails'))
  //   return <Redirect push to='/login' />;
  return (
    <div className='antlegs'>
      <div className='app-container'>
        {location.pathname === '/login' ? (
          <Route path='/login' component={UserEntry} />
        ) : (
          <div>
            <AlDrawer
              isOpen={isDrawerOpen}
              toggleDrawer={toggleDrawer}
              position='left'
              dataToDisplay={
                <AlSideBarItem currentPageName={currentPageName} />
              }
            />
            <AlHeader toggleDrawer={toggleDrawer} />
            <div className='render-area'>
              <AlBreadcrumbs currentPageName={currentPageName} />
              <Routes />
            </div>
            <div className='al-footer d-flex-center w-100'>
              Powered by: Antlegs Technology Solutions Private Limited.
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default withRouter(App);
