import React from 'react';
import AlCard from '../../components/al-card';
import { Chip } from '@mui/material';
import { useHistory } from 'react-router-dom';

const CandidateCard = () => {
  const history = useHistory();

  const onCardClick = () => {
    history.push('/candidate-details/1');
  };
  const RestOfElm = ({ designation, experience, skills }) => {
    return (
      <>
        <div className='card-info-text'>{designation}</div>
        <div className='card-info-text'>
          Total Experience: {experience} years
        </div>
        <div className='card-info-text'>
          <div className='d-flex badge-values h-center'>
            <span>Skills:</span>
            {skills?.map((val) => (
              <Chip label={val} size='small' />
            ))}
          </div>
        </div>
      </>
    );
  };
  return (
    <AlCard
      title='Maneesh Kumar'
      ratingValue={4}
      status='No - Activity'
      lastActivity='Aug 11, 2022'
      onClick={onCardClick}
      restOfElm={
        <RestOfElm
          designation='Service Quality Manager @ HDFC Bank '
          experience='7'
          skills={[
            'React',
            'Django',
            'React',
            'Django',
            'React',
            'Django',
            'Process Re-engineering',
          ]}
        />
      }
    />
  );
};

export default CandidateCard;
