import React, { useState } from 'react';
import AlInfoCard from '../../../components/al-info-card';
import DoneOutlineIcon from '@mui/icons-material/DoneOutline';
import AlBtn from '../../../components/al-btn';
import AlInput from '../../../components/al-input';

const RecruiterBankDetails = () => {
  const [isEditMode, toggleEditMode] = useState(false);
  const [bankFormData, updateBankFormData] = useState({
    addressLineOne: '',
    addressLinetwo: '',
    city: '',
    country: '',
    pincode: '',
  });
  const bankDetailsElements = [
    {
      label: 'Branch Name',
      id: 'gstNumber',
      type: '',
    },
    {
      label: 'Address Line 1',
      id: 'panNumber',
      type: '',
    },
    {
      label: 'Address Line 2',
      id: 'panNumber',
      type: '',
    },
    {
      label: 'Location',
      id: 'panNumber',
      type: '',
    },
    {
      label: 'Pincode',
      id: 'panNumber',
      type: '',
    },
  ];

  const BankDetails = ({
    addressLineOne,
    addressLinetwo,
    city,
    country,
    pincode,
  }) => {
    return (
      <>
        <div className='info-card-info-text '>{addressLineOne}</div>
        {addressLinetwo && (
          <div className='info-card-info-text '>{addressLinetwo}</div>
        )}
        <div className='info-card-info-text '>
          {city} {country}
        </div>
        <div className='info-card-info-text '>{pincode}</div>
      </>
    );
  };
  const onActionItemClick = (action) => {
    if (action === 'edit') {
      toggleEditMode(true);
    }
  };
  const updateBankFormVal = (id, val) => {
    updateBankFormData({
      ...bankFormData,
      [id]: val,
    });
  };
  return (
    <div className='d-flex recruiter-bank-details'>
      {isEditMode ? (
        <div>
          <div className='sub-header'>Edit Details</div>
          <div className='d-flex'>
            {bankDetailsElements.map((elm) => (
              <AlInput
                label={elm.label}
                onChange={(val) => updateBankFormVal(elm.id, val)}
              />
            ))}
          </div>
          <div className='d-flex action-btn'>
            <AlBtn
              label='Cancel'
              variant='outlined'
              onClick={() => toggleEditMode(false)}
            />
            <AlBtn label='Save' variant='contained' />
          </div>
        </div>
      ) : (
        <>
          <AlInfoCard
            title='Chennai Branch'
            extraActionItem={[
              {
                displayElm: () => (
                  <div className='d-flex-center'>
                    <DoneOutlineIcon sx={{ fontSize: 15, color: '#949494' }} />{' '}
                    Set as default
                  </div>
                ),
                value: 'edit',
              },
            ]}
            restOfElm={
              <BankDetails
                addressLineOne='Anna salai'
                city='Chennai, Tamil Nadu'
                country='India'
                pincode='600018'
              />
            }
            onActionItemClick={onActionItemClick}
          />
          <div className='add-branch'>
            <AlBtn label='Add' variant='contained' />
          </div>
        </>
      )}
    </div>
  );
};

export default RecruiterBankDetails;
