import React, { useState } from 'react';

import Button from '@mui/material/Button';

import AlCheckbox from '../../../../../components/al-checkbox';
import AlInput from '../../../../../components/al-input';
import './createEmployer.scss';
import { createAccountService } from '../createAccountService';

const CreateEmployer = () => {
  const [userData, updateUserData] = useState({
    email: '',
    companyName: '',
    userType: 'employer',
    dialCode: '+91',
    contactNo: '',
    fullName: '',
    contactName: '',
    website: '',
    rule: 'regUser',
    serviceFee: [],
    invoiceAddress: [],
  });
  const createUser = async () => {
    const res = await createAccountService(userData);
    if (res) {
      alert('user created successfully');
    }
  };
  const updateUserVal = (id, val) => {
    updateUserData({
      ...userData,
      [id]: val,
    });
  };
  return (
    <div className='create-recruiter'>
      <div className='header'>Employer</div>
      <AlInput
        label='Company Name'
        value={userData.companyName}
        onChange={(val) => {
          updateUserVal('companyName', val);
        }}
      />
      <AlInput
        label='Website'
        value={userData.website}
        onChange={(val) => {
          updateUserVal('website', val);
        }}
      />
      <AlInput
        label='Contact Name'
        value={userData.contactName}
        onChange={(val) => {
          updateUserVal('contactName', val);
        }}
      />
      <AlInput
        label='Contact Number'
        value={userData.contactNo}
        onChange={(val) => {
          updateUserVal('contactNo', val);
        }}
      />
      <AlInput
        label='Email'
        value={userData.email}
        onChange={(val) => {
          updateUserVal('email', val);
        }}
      />
      <AlCheckbox label='I agree & accept the Terms & Conditions' />
      <Button variant='contained' className='primary-btn' onClick={createUser}>
        Register
      </Button>
      <Button variant='contained' className='primary-btn'>
        Back
      </Button>
    </div>
  );
};

export default CreateEmployer;
