import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import './style.scss';
import Previous from './previous.png';


const AlBreadcrumbs = ({ currentPageName }) => {
  const history = useHistory();

  const navigateBack = () => {
    history.goBack();
  };
  return (
    <div className='al-breadcrumbs'>
      <div className='navigateback' onClick={navigateBack}>
        <img src={Previous} />
      </div>
      <div>{currentPageName}</div>
    </div>
  );
};

export default AlBreadcrumbs;
