import React, { useState } from 'react';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import ModeEditOutlineOutlinedIcon from '@mui/icons-material/ModeEditOutlineOutlined';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import './style.scss';
import AlMenu from '../al-menu';

const AlInfoCard = ({
  title,
  extraActionItem = [],
  lastActivityTitle,
  lastActivity,
  restOfElm,
  onActionItemClick
}) => {
  const [actionElm, setActionElm] = useState(null);

  const actionItemElm = [
    ...extraActionItem,
    {
      displayElm: () => (
        <div className='d-flex-center'>
          <ModeEditOutlineOutlinedIcon
            sx={{ fontSize: 15, color: '#949494' }}
          />{' '}
          Edit
        </div>
      ),
      value: 'edit',
    },
    {
      displayElm: () => (
        <div className='d-flex-center'>
          <DeleteOutlineIcon sx={{ fontSize: 15, color: '#949494' }} /> Delete
        </div>
      ),
      value: 'delete',
    },
  ];
  const onActionClick = (e) => {
    setActionElm(e.currentTarget);
  };
  const triggerAction = (val) => {
    onActionItemClick(val)
    setActionElm(null);
  };

  return (
    <div className='al-info-card'>
      <div className='info-card-info-wrapper'>
        <div className='info-card-info-bar'>
          <div className='info-action' onClick={(e) => onActionClick(e)}>
            <MoreVertIcon />
          </div>
          <AlMenu
            actionElm={actionElm}
            triggerAction={triggerAction}
            menuItems={actionItemElm}
          />
        </div>
        <div className='info-card-header'>{title}</div>
        {restOfElm}
        {lastActivity && (
          <div className='info-card-activity'>
            {lastActivityTitle || 'Last Activity'}: {lastActivity}
          </div>
        )}
      </div>
    </div>
  );
};

export default AlInfoCard;
