import React from 'react';
import './card.scss';
import AlBadge from '../al-badge';
import AlRating from '../al-rating';

const AlCard = ({
  title,
  logo,
  status,
  lastActivityTitle,
  lastActivity,
  ratingValue,
  onClick,
  restOfElm,
}) => {
  return (
    <div className='card' onClick={onClick}>
      <div className='card-info-wrapper'>
        <div className='card-info-bar'>
          {ratingValue ? (
            <div className='rating-holder'>
              <AlRating ratingValue={ratingValue} size='small' />
            </div>
          ) : (
            <div className='image-holder'>
              <img src={logo} />
            </div>
          )}
          <div className='card-status'>
            <AlBadge status={status} />
          </div>
        </div>

        <div className='card-header'>{title}</div>
        {restOfElm}
      </div>
      <div className='card-activity'>
        {lastActivityTitle || 'Last Activity'}: {lastActivity}
      </div>
    </div>
  );
};

export default AlCard;
