import Axios from 'axios';

class APIS {
  constructor(_endpoint) {
    const domain = ' https://or26zz9ksj.execute-api.ap-south-1.amazonaws.com';
    const prefix = '/dev/antlegs/api';
    const endpoint = _endpoint || domain + prefix;
    const urls = {
      SIGN_IN: () => '/home/login/',
      GET_JOBS: () => '/job/',
      CREATE_JOB: () => '/job/',
      CREATE_ACCOUNT: () => '/home/setregistration'
    };

    this.domain = _endpoint ? '' : domain;
    this.prefix = _endpoint ? '' : prefix;
    this.endpoint = endpoint;
    this.urls = urls;
    this.get_url = this.get_url.bind(this);
  }

  get_url(name, ...data) {
    const url = this.urls[name](...data);
    return `${this.endpoint}${url}`;
  }
  get(name, params, ...data) {
    return Axios({
      method: 'get',
      url: this.get_url(name, ...data),
      responseType: 'json',
      params,
      timeout: 60 * 1000,
      // headers: {
      //   HTTP_X_USERNAME: 'santhosh',
      // },
    })
      .then((res) => {
        const { status, data } = res;
        return {
          status: status === 200,
          ...data,
        };
      })
      .catch((error) => ({
        status: false,
      }));
  }
  put(name, payloadData, ...data) {
    return Axios({
      method: 'put',
      url: this.get_url(name, ...data),
      responseType: 'json',
      timeout: 60 * 1000,
      data: payloadData,
      // headers: {
      //   HTTP_X_USERNAME: 'santhosh',
      // },
    });
  }
  post(name, payloadData, ...data) {
    console.log('this.get_url(name, ...data)', this.get_url(name, ...data));
    return Axios({
      method: 'post',
      url: this.get_url(name, ...data),
      responseType: 'json',
      timeout: 60 * 1000,
      data: payloadData,
      // headers: {
      //   HTTP_X_USERNAME: 'santhosh',
      // },
    })
      .then((res) => {
        const { status, data } = res;
        return {
          status: status === 200,
          ...data,
        };
      })
      .catch((error) => ({
        status: false,
      }));
  }
}

const Apis = new APIS();
export { Apis as APIS };
