import React, { useState } from 'react';
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';
import AlInput from '../al-input';
import ClearIcon from '@mui/icons-material/Clear';
import './style.scss';
const AlAutocompleteBadge = ({ values, onValAdd }) => {
  const [valueList, onValUpdate] = useState(values || []);
  const handleKeyDown = (event, val) => {
    if (event.key === 'Enter') {
      const updateList = [...valueList, val]
      onValUpdate(updateList)
      onValAdd(updateList)
    }
  };
  const removeVal = () => {};
  return (
    <div className='al-autocomplete-badge'>
      <AlInput onKeyDown={handleKeyDown} onKeyEnter={true} />
      <div className='badge-values'>
        <Stack direction='row' spacing={1}>
          {valueList.map((val) => (
            <Chip label={val} onDelete={removeVal} deleteIcon={<ClearIcon />} />
          ))}
        </Stack>
      </div>
    </div>
  );
};

export default AlAutocompleteBadge;
