import React from 'react';
import './userTypeToggle.scss';
import Radio from '@mui/material/Radio';

const UserTypeToggle = ({ isSelected, userType, onSelectUserType }) => {
  return (
    <div className='user-toggle d-flex h-center'>
      <div>
        <Radio
          checked={isSelected}
          onClick={() => onSelectUserType(userType)}
          sx={{
            color: '#b4b4bc',
            '&.Mui-checked': {
              color: '#ffcc35',
            },
            '& .MuiSvgIcon-root': {
              fontSize: 28,
            },
          }}
        />
      </div>
      <div className='display-message'>
        <div>
          I'm {userType === 'Employer' ? 'an ' : 'a'}{' '}
          <span className='primary-blue'>{userType}</span>,
        </div>
        <div>Looking for Candidates</div>
      </div>
    </div>
  );
};

export default UserTypeToggle;
