import React from 'react';
import AlFilterBar from '../../components/al-filter-bar';
import AlBtn from '../../components/al-btn';
import logo from './Flipkart-Logo.png';
import AlCard from '../../components/al-card';
import { useHistory } from 'react-router-dom';
import './style.scss';

const Employers = () => {
  const history = useHistory();

  const onCardClick = () => {
    history.push('/employer-details/1');
  };
  const RestOfElm = ({ location, industry, website }) => {
    return (
      <>
        <div className='card-info-text mat-10'> {industry}</div>
        <div className='card-info-text'>{location}</div>
        <div className='card-info-text mat-10'>{website}</div>
      </>
    );
  };
  return (
    <div className='manage-employers d-flex'>
      <AlFilterBar />
      <AlCard
        title='Flipkart'
        onClick={onCardClick}
        logo={logo}
        status='New'
        lastActivityTitle='Registered On '
        lastActivity='Aug 11, 2022'
        restOfElm={
          <RestOfElm
            location='India'
            industry='Information Technology'
            website='www.flipkart.com'
          />
        }
      />
    </div>
  );
};

export default Employers;
