import React from 'react';
import FullCalendar from '@fullcalendar/react';
import dayGridPlugin from '@fullcalendar/daygrid';
import './style.scss';

const events = [
  { title: 'Interview Scheduled (5)', start: new Date(), viewMore: () => {} },
];

function renderEventContent(eventInfo) {
  return (
    <div className='w-100'>
      <div className='event-info d-flex w-100'>{eventInfo.event.title}</div>
      <div
        className='view-details d-flex w-100'
        onClick={() => eventInfo.event.viewMore()}
      >
        View Details
      </div>
    </div>
  );
}
const AlCalendar = ({ onDetailsClick }) => {
  return (
    <div className='al-full-calendar'>
      <FullCalendar
        customButtons={{
          custom1: {
            text: 'Details',
            click: () => onDetailsClick(),
          },
        }}
        headerToolbar={{
          start: 'dayGridMonth dayGridWeek',
          center: 'prev title next',
          end: 'today custom1',
        }}
        plugins={[dayGridPlugin]}
        views={['dayGridMonth', 'dayGridWeek']}
        weekends={true}
        events={events}
        eventContent={renderEventContent}
      />
    </div>
  );
};

export default AlCalendar;
