import React, { useState } from 'react';
import AlCalendar from '../../components/al-calendar';
import './style.scss';
import InterviewListViews from './interviewListView';

const Interviews = () => {
  const [isCalendarView, toggleCalendarView] = useState(true);
  const onDetailsClick = () => {
    toggleCalendarView(false);
  };
  return (
    <div className='interviews '>
      {isCalendarView ? (
        <AlCalendar onDetailsClick={onDetailsClick} />
      ) : (
        <InterviewListViews />
      )}
    </div>
  );
};

export default Interviews;
