import React, { useState } from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import './style.scss';

const AlTabs = ({ tabs }) => {
  const [value, setValue] = useState(0);

  const TabPanel = (props) => {
    const { elm, value, index, other } = props;
    return (
      <div
      className='al-tabs-panel'
        role='tabpanel'
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && elm} 
      </div>
    );
  };
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  return (
    <div className='al-tabs'>
      <Tabs value={value} onChange={handleChange} aria-label='Al tabs'>
        {tabs.map((tab) => (
          <Tab label={tab?.title} />
        ))}
      </Tabs>
      {tabs.map((tab, index) => (
        <TabPanel value={value} index={index} elm={tab.elm}/>
      ))}
    </div>
  );
};

export default AlTabs;
