import React from 'react';

import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';

import './style.scss';

const AlSelect = ({ label, menuItem, onChange, selectedValue }) => {
  return (
    <div className='al-select'>
      <FormControl fullWidth size='small'>
        {label ? <InputLabel id='al-select'>{label}</InputLabel> : null}
        <Select
          labelId='al-select'
          id='al-select'
          label={label}
          onChange={onChange}
          value={selectedValue}
        >
          {menuItem?.map((item) => (
            <MenuItem value={item.value}>{item.displayName}</MenuItem>
          ))}
        </Select>
      </FormControl>
    </div>
  );
};

export default AlSelect;
