import React, { useState } from 'react';

import Button from '@mui/material/Button';
import { LoginService } from '../../service/loginService';
import AlCheckbox from '../../../../components/al-checkbox';
import AlInput from '../../../../components/al-input';
import './login.scss';
import { useHistory } from 'react-router-dom';

const Login = () => {
  const [userName, setUserName] = useState('');
  const [password, setPassword] = useState('');
  const history = useHistory();
  const onLoginBtnClick = async () => {
    const loginRes = await LoginService({ userName, password });
    if (loginRes.status === 'success') {
      localStorage.setItem('userDetail', JSON.stringify(loginRes));
      history.push('/dashboard');
    }
  };
  return (
    <div className='login'>
      <div className='header'>Sign-In</div>
      <div className='login-form'>
        <AlInput label='Registered Email ID' onChange={setUserName} />
        <AlInput label='Password' onChange={setPassword} type='password' />
        <div className='d-flex h-center login-action'>
          <AlCheckbox label='Remember Me' />
          <div>Forgot Password?</div>
        </div>
        <Button
          variant='contained'
          className='primary-btn'
          onClick={onLoginBtnClick}
        >
          Sign - In
        </Button>
      </div>
    </div>
  );
};

export default Login;
