import React, { useState } from 'react';
import TextField from '@mui/material/TextField';
import SearchOutlinedIcon from '@mui/icons-material/SearchOutlined';

import './alInput.scss';

const AlInput = ({
  label,
  value,
  placeholder,
  type,
  isSearch,
  onChange,
  onFocusOut,
  onKeyDown,
  onKeyEnter
}) => {
  const [inputValue, setInputValue] = useState(value);
  return (
    <div className='d-flex al-input'>
      <span className='form-label'>{label}</span>
      <TextField
        value={inputValue}
        variant='outlined'
        placeholder={placeholder}
        type={type}
        onChange={(e) => {
          setInputValue(e.target.value);
          if (onChange) {
            onChange(e.target.value);
          }
        }}
        onFocusOut={(e) => {
          onFocusOut && onFocusOut(e.target.value);
        }}
        onKeyDown={(e) => {
          if (onKeyDown && onKeyEnter && e.key === 'Enter') {
            onKeyDown(e, e.target.value);
            setInputValue('');
          } else if (onKeyDown) {
            onKeyDown(e, e.target.value);
          }
        }}
        InputProps={{
          endAdornment: isSearch && <SearchOutlinedIcon />,
        }}
      />
    </div>
  );
};

export default AlInput;
