import React from 'react';
import Slider from '@mui/material/Slider';
import './style.scss'
const AlSlider = ({label, value, min, max, updateValue, minLabel, maxLabel}) => {
  return (
    <div className='al-slider'>
      <div className='form-label'>{label}</div>
      <Slider
        value={value}
        min={min}
        max={max}
        onChange={(e, val) => updateValue(val)}
        valueLabelDisplay='auto'
      />
      <div className='d-flex h-center space-between'>
        <div className='form-label'>{minLabel}</div>
        <div className='form-label'>{maxLabel}</div>
      </div>
    </div>
  );
};

export default AlSlider;
