import React, { useState } from 'react';
import AlInfoCard from '../../../components/al-info-card';
import AlBtn from '../../../components/al-btn';
import DoneOutlineIcon from '@mui/icons-material/DoneOutline';
import LocalPhoneOutlinedIcon from '@mui/icons-material/LocalPhoneOutlined';
import AlInput from '../../../components/al-input';

const EmployerInvoiceDetails = () => {
  const [isEditMode, toggleEditMode] = useState(false);
  const [invoiceFormData, updateInvoiceformData] = useState({
    addressLineOne: '',
    addressLinetwo: '',
    city: '',
    country: '',
    pincode: '',
  });
  const invoiceDetailsElements = [
    {
      label: 'Location Name',
      id: 'gstNumber',
      type: '',
    },
    {
      label: 'Address Line 1',
      id: 'panNumber',
      type: '',
    },
    {
      label: 'Address Line 2',
      id: 'panNumber',
      type: '',
    },
    {
      label: 'Location',
      id: 'panNumber',
      type: '',
    },
    {
      label: 'Pincode',
      id: 'panNumber',
      type: '',
    },
    {
      label: 'Contact No',
      id: 'panNumber',
      type: '',
    },
  ];;
  const AddressDetails = ({
    addressLineOne,
    addressLinetwo,
    city,
    country,
    pincode,
    contactNo,
  }) => {
    return (
      <>
        <div className='info-card-info-text '>{addressLineOne}</div>
        {addressLinetwo && (
          <div className='info-card-info-text '>{addressLinetwo}</div>
        )}
        <div className='info-card-info-text '>
          {city} {country}
        </div>
        <div className='info-card-info-text '>{pincode}</div>
        <div className='info-card-info-text mat-10 d-flex'>
          <LocalPhoneOutlinedIcon
            sx={{ fontSize: 15, color: '#4578C8', marginRight: '5px' }}
          />
          {contactNo}
        </div>
      </>
    );
  };
  const onActionItemClick = (action) => {
    if (action === 'edit') {
      toggleEditMode(true);
    }
  };
  const updateInvoiceFormVal = (id, val) => {
    updateInvoiceformData({
      ...invoiceFormData,
      [id]: val,
    });
  };
  return (
    <div className='d-flex invoice-details'>
      {isEditMode ? (
        <div>
          <div className='sub-header'>Edit Details</div>
          <div className='d-flex'>
            {invoiceDetailsElements.map((elm) => (
              <AlInput
                label={elm.label}
                onChange={(val) => updateInvoiceFormVal(elm.id, val)}
              />
            ))}
          </div>
          <div className='d-flex action-btn'>
            <AlBtn
              label='Cancel'
              variant='outlined'
              onClick={() => toggleEditMode(false)}
            />
            <AlBtn label='Save' variant='contained' />
          </div>
        </div>
      ) : (
        <>
          <AlInfoCard
            title='Chennai '
            onActionItemClick={onActionItemClick}
            extraActionItem={[
              {
                displayElm: () => (
                  <div className='d-flex-center'>
                    <DoneOutlineIcon sx={{ fontSize: 15, color: '#949494' }} />{' '}
                    Set as default
                  </div>
                ),
                value: 'edit',
              },
            ]}
            restOfElm={
              <AddressDetails
                addressLineOne='Anna salai'
                city='Chennai, Tamil Nadu'
                country='India'
                pincode='600018'
                contactNo='9626171661'
              />
            }
          />
          <div className='add-invoice-location'>
            <AlBtn label='Add' variant='contained' />
          </div>
        </>
      )}
    </div>
  );
};

export default EmployerInvoiceDetails;
