import React, { useState } from 'react';
import AlInput from '../../../components/al-input';

const EmployerProfile = () => {
  const [profileFormData, updateProfileFormData] = useState({
    fullName: '',
    displayName: '',
    industry: '',
    location: '',
    website: '',
    currency: '',
    contactName: '',
    designation: '',
    email: '',
    contactNo: '',
    gstNumber: '',
    panNumber: '',
  });
  const employerDetailsElements = [
    {
      label: 'Name',
      id: 'fullName',
      type: '',
    },
    {
      label: 'Display Name',
      id: 'displayName',
      type: '',
    },
    {
      label: 'Industry',
      id: 'industry',
      type: '',
    },
    {
      label: 'Location',
      id: 'location',
      type: '',
    },
    {
      label: 'Website',
      id: 'website',
      type: '',
    },
    {
      label: 'Currency',
      id: 'currency',
      type: '',
    },
  ];
  const contactDetailsElements = [
    {
      label: 'Contact Person',
      id: 'contactName',
      type: '',
    },
    {
      label: 'Designation',
      id: 'designation',
      type: '',
    },
    {
      label: 'Email',
      id: 'email',
      type: '',
    },
    {
      label: 'Contact No',
      id: 'contactNo',
      type: '',
    },
  ];
  const taxDetailsElements = [
    {
      label: 'GST NO',
      id: 'gstNumber',
      type: '',
    },
    {
      label: 'PAN Details',
      id: 'panNumber',
      type: '',
    },
  ];
  const updateProfileFormVal = (id, val) => {
    updateProfileFormData({
      ...profileFormData,
      [id]: val,
    });
  };
  return (
    <div className='employer-profile'>
      <div className='sub-header'>Employer Details</div>
      <div className='d-flex'>
        {employerDetailsElements.map((elm) => (
          <AlInput
            label={elm.label}
            onChange={(val) => updateProfileFormVal(elm.id, val)}
          />
        ))}
      </div>
      <div className='split-border'></div>
      <div className='sub-header'>Contact Details</div>
      <div className='d-flex'>
        {contactDetailsElements.map((elm) => (
          <AlInput
            label={elm.label}
            onChange={(val) => updateProfileFormVal(elm.id, val)}
          />
        ))}
      </div>
      <div className='split-border'></div>
      <div className='sub-header'>Tax Details</div>
      <div className='d-flex'>
        {taxDetailsElements.map((elm) => (
          <AlInput
            label={elm.label}
            onChange={(val) => updateProfileFormVal(elm.id, val)}
          />
        ))}
      </div>
    </div>
  );
};

export default EmployerProfile;
